* {
  box-sizing: border-box;
}

p {
  margin: 0;
}

.card {
  display: flex;
  flex-direction: column;
  width: 200px;
  background-color: var(--background-color);
  border: 1px solid var(--border-color);
  border-radius: 5px;
}

.card__image {
  margin-top: 32px;
  width: 160px;
  height: 134px;
  align-self: center;
}

.card__text {
  margin: 40px 0 16px;
}

.card__text_monoblock {
  margin: 0;
  margin-left: 16px;
  display: flex;
  font-weight: 500;
  font-size: 12px;
  color: var(--text-color);
  line-height: 18px;
}

.card__text_code {
  margin-top: 4px;
  margin-left: 16px;
  font-size: 10px;
  line-height: 14px;
  color: var(--second-text-color);
}

.stars {
  display: flex;
  flex-direction: row;
  align-items: center;

  &__star {
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    width: 16px;
    height: 16px;
    background-position: center;
    

    &:not(:last-child) {
      margin-right: 4px;
    }

    &:first-child {
      margin-left: 16px;
    }
  }

  @for $index from 1 through 5 {
    &--#{$index} :nth-child(-n+ #{$index}) {
      background-image: url(../images/star-active.svg);
    }
  }
}

.stars__reviews {
  flex-grow: 1;
  margin-right: 16px;
  text-align: right;
  font-size: 10px;
  line-height: 14px;
  color: var(--text-color);
}

.card__price {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 24px;
}

.card__price_word {
  flex-grow: 1;
  margin-left: 16px;
  font-size: 12px;
  line-height: 18px;
  color: var(--second-text-color);
}

.card__price_sum {
  margin-right: 16px;
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: var(--text-color);
}

.card__button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  text-align: center;
  margin: 16px;
  background-color: var(--second-background-color);
  border-radius: 5px;
  text-decoration: none;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: var(--background-color);
}

.card__button:hover {
  background-color: unset;
  border: 1px solid var(--second-background-color);
  color: var(--second-background-color);
}
